<template>
  <v-container v-if="isloaded">
    <v-row>
      <v-col cols="6">
        <v-text-field
          outlined
          label="ホルダー10連確率(%)"
          v-model.number="input_p1"
          class="mb-n8"
        ></v-text-field>
      </v-col>
      <v-col cols="6">
        <v-text-field
          outlined
          label="単発確率(%)"
          v-model.number="input_p2"
          class="mb-n8"
        ></v-text-field>
      </v-col>

      <v-col cols="6">
        <v-text-field
          outlined
          label="ホルダー10連(連)"
          v-model="input_n0"
          id="n0_input"
          @input="AddZero"
          class="mb-n8"
        ></v-text-field>
      </v-col>
      <v-col cols="6">
        <v-text-field
          outlined
          label="単発(連)"
          v-model.number="input_n2"
          class="mb-n8"
        ></v-text-field>
      </v-col>

      <v-col cols="6">
        <v-text-field
          outlined
          label="ホルダー10連リトライ数(回)"
          v-model.number="input_retry"
          class="mb-n8"
        ></v-text-field>
      </v-col>

      <v-col cols="6">
        <v-btn @click="this.refleshGraph" style="right: -50%; bottom: -20%">
          計算
        </v-btn>
      </v-col>
    </v-row>

    <canvas id="output"></canvas>
  </v-container>
</template>

<style>
#output {
  width: 80%;
  max-width: 600px;
  /* height:50vw!important; */
}
</style>

<script>
const wasm_prom = import("../../../../pkg");
const graph = require("chart.js");
export default {
  name: "HelloWorld",

  data: () => ({
    input_p1: undefined,
    input_p2: undefined,
    input_n0: undefined,
    input_n2: undefined,
    input_retry: undefined,

    isloaded: false,
  }),

  methods: {
    makeGraph(outputGraph) {
      let ctx = document.getElementById("output");
      //eslint-disable-next-line
      let myChart = new graph(ctx, { 
        // グラフの形式を指定
        type: "line",
        // グラフのデータを指定
        data: {
          // X軸の見出し設定
          labels: [...range(6), "6以上"],
          // 各種グラフ設定
          datasets: [
            {
              // グラフの見出し設定
              label: "x軸:引く枚数 y軸:確率(%)",
              // Y軸のデータ設定
              data: outputGraph,
              borderWidth: 1,
              fill: "boundary",
              backgroundColor: "red",
              borderColor: "red",
            },
          ],
        },
        // グラフ自体の共通項目設置絵
        options: {
          // レスポンシブ形式を無効にする
          responsive: true,
          spanGaps: false,
          elements: {
            line: {
              tension: 0.000001,
            },
          },
        },
      });

      function range(length) {
        return iter_array(0, length);
      }

      function iter_array(to, from) {
        return new Array(from - to).fill(null).map((_, i) => to + i);
      }
    },
    calc(mx_retry, p1, p2, n0, n2) {
      const pk1 = array_Binomial_Distribution.bind(this)(
        iter_array(0, 10 + 1),
        10,
        p1
      );
      const pk2 = array_Binomial_Distribution.bind(this)(
        iter_array(0, n2 + 1),
        n2,
        p2
      );

      function f_multi_retry(mx_retry) {
        let dp = Multi_array(mx_retry + 1, 1);
        dp[0][0] = 1;
        //eslint-disable-next-line
        for (let _ of range(n0)) {
          let dp2 = Multi_array(mx_retry + 1, dp[0].length + 10);
          for (let i of range(mx_retry + 1)) {
            for (let j of range(dp[0].length)) {
              for (let k of range(10 + 1)) {
                if (i !== mx_retry && k === 0) {
                  dp[i + 1][j] += dp[i][j] * pk1[0];
                } else {
                  dp2[i][j + k] += dp[i][j] * pk1[k];
                }
              }
            }
          }
          dp = dp2;
        }
        let dp2 = Multi_array(mx_retry + 1, dp[0].length + n2);
        for (let i of range(dp[0].length)) {
          for (let j of range(n2 + 1)) {
            for (let k of range(mx_retry + 1)) {
              dp2[k][i + j] += dp[k][i] * pk2[j];
            }
          }
        }
        dp = dp2;
        // console.log(dp);
        return array_sum_y(dp);
      }

      function array_Binomial_Distribution(n, k, p) {
        let output = [];
        for (let i of n) {
          output.push(Number(this.wasm.Binomial_Distribution(k, i, p)));
        }
        return output;
      }

      function array_sum_y(array) {
        let output = new Array(array[0].length);
        for (let y = 0; y < array[0].length; y++) {
          output[y] = 0;
          for (let x = 0; x < array.length; x++) {
            output[y] += array[x][y];
          }
        }
        return output;
      }

      function range(length) {
        return iter_array(0, length);
      }

      function Multi_array(x, y) {
        return Array.from(new Array(x), () => new Array(y).fill(0));
      }

      function iter_array(to, from) {
        return new Array(from - to).fill(null).map((_, i) => to + i);
      }

      let result = f_multi_retry(mx_retry);
      let gotCharaChance_Zero2five = 0;

      for (let i of range(5 + 1)) {
        gotCharaChance_Zero2five += result[i];
      }
      let higher6Chance = 1 - gotCharaChance_Zero2five; 

      let outputGraph = new Array(7);
      for (let i of range(5 + 1)) {
        outputGraph[i] = result[i] || 0;
      }

      outputGraph[6] = higher6Chance || 0;

      return outputGraph.map((x) => x * 100);
    },
    refleshGraph() {
      // const mx_retry = 2 //retry

      const p1 = this.input_p1 / 100 || 0; // 10連ホルダー確率
      const p2 = this.input_p2 / 100 || 0; // 通常
      const n0 = this.input_n0 / 10 || 0;
      // const n1 = 10*n0  //eslint-disable-line
      const n2 = this.input_n2 || 0;
      const mx_retry = this.input_retry || 0;

      this.makeGraph(this.calc(mx_retry, p1, p2, n0, n2));
    },
    AddZero(input) {
      let n0 = Number(input) || 0;
      if (n0 % 10 !== 0) {
        // 小数消す
        if (n0 % 1 !== 0) {
          n0 -= n0 % 1;
        }
        n0 *= 10;
      }

      this.input_n0 = n0;

      this.$nextTick(() => {
        const num_length = String(n0).length - 1;

        document
          .getElementById("n0_input")
          .setSelectionRange(num_length, num_length);
      });
    },
  },

  async mounted() {
    this.wasm = await wasm_prom;

    this.isloaded = true;
    this.$nextTick(this.refleshGraph);
    // ()
  },
};
</script>
